@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
html,
body,
#root,
.app {
  height: 100%;
}

.month {
  height: 100%;
  display: grid;
  grid-template-rows: 5% auto;
  padding: 1rem;
}

.month .heading {
  text-align: center;
  align-self: center;
  font-family: "Great Vibes", cursive;
  font-size: 42px;
  color: #ab1111;
  font-weight: 600;
}

.month .days {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 2px;
}

@media screen and (max-width: 950px) {
  .month .days {
    grid-template-columns: repeat(5, 1fr);
  }
}
@media screen and (max-width: 800px) {
  .month .days {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 400px) {
  .month .days {
    grid-template-columns: 1fr;
  }
}

.day {
  padding: 1.5rem;
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: bottom;
  opacity: 0.7;
  box-shadow: 1px 1px 6px 0px black;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.day.sunday {
  background-size: 40%;
}

.sunday {
  background-image: url(/calendar/static/media/sunday.0256b078.svg);
}
.monday {
  background-image: url(/calendar/static/media/monday.6af16566.svg);
}
.tuesday {
  background-image: url(/calendar/static/media/tuesday.c5117fae.svg);
}
.wednesday {
  background-image: url(/calendar/static/media/wednesday.686bfb6b.svg);
}
.thursday {
  background-image: url(/calendar/static/media/thursday.76a23e16.svg);
}
.friday {
  background-image: url(/calendar/static/media/friday.664aa5d7.svg);
}
.saturday {
  background-image: url(/calendar/static/media/saturday.64d0c4b5.svg);
}

.weekday {
  margin: 0 auto;
  font-family: "Great Vibes", cursive;
  font-size: 1.5rem;
}

.wednesday .weekday {
  color: #f9a727;
  text-shadow: 1px 1px #3f3d55;
}
.saturday .weekday {
  color: #e50914;
  text-shadow: 1px 1px #00bfa6;
}
.thursday .weekday {
  color: #2f2f41;
  text-shadow: 1px 1px #00b0ff;
}
.friday .weekday {
  color: #d83e75;
  text-shadow: 1px 1px #2f2f41;
}
.sunday .weekday {
  color: #ff6584;
  text-shadow: 1px 1px #3f3d55;
}
.monday .weekday {
  color: #0e01bf;
  text-shadow: 1px 1px #ffb8b8;
}
.tuesday .weekday {
  color: #36799a;
  text-shadow: 1px 1px #ffb8b8;
}

.today {
  box-shadow: 0px 0px 5px 2px #770909;
  opacity: 1;
}

